.search {
  width: 320px;
  margin: 0 auto;
  padding-bottom: 24px;
}

.search__wrapper {
  padding: 25px 20px 49px 20px;

  font-kerning: none; /* Pixelperfect */
}

.search__title {
  margin: 0;
  padding-bottom: 20px;

  font-size: 21px;
}

.search__description {
  margin: 0;
  padding: 0 0 34px 0;

  font-size: 14px;
  line-height: 24px;

  color: #333333;
}

.search__break {
  display: none;
}

.search__link {
  .button;
}

.search__map {
  margin: 0;
}

.search__map iframe {
  width: 100%;
  height: 350px;

  background-image: url("../img/bg-map-mobile.jpg");
  border: none;

  @media (min-resolution: @retina-dpi), (min-resolution: @retina-dppx) {
    background-image: url("../img/bg-map-mobile@2x.jpg");
  }
}

/* ---------- Планшетная версия ---------- */

@media (min-width: @tablet-width) {
  .search {
    width: 768px;
    padding-bottom: 0;
  }

  .search__wrapper {
    padding: 55px 20.1% 58px 20.1%;
  }

  .search__title {
    padding-bottom: 31px;

    font-size: 30px;
  }

  .search__description {
    padding: 0 0 42px 0;
  }

  .search__break {
    display: block;
  }

  .search__map iframe {
    background-image: url("../img/bg-map-tablet.jpg");

    @media (min-resolution: @retina-dpi), (min-resolution: @retina-dppx) {
      background-image: url("../img/bg-map-tablet@2x.jpg");
    }
  }

  .search__link {
    width: 459px;

    box-sizing: border-box;
  }
}

/* ---------- Десктопная версия ---------- */

@media (min-width: @desktop-width) {
  .search {
    width: 1200px;
  }

  .search__wrapper {
    padding: 55px 31% 65px 31%;
  }

  .search__description {
    padding: 0 0 47px 0;
  }

  .search__link {
    width: 457px;
  }

  .search__map iframe {
    background-image: url("../img/bg-map-desktop.jpg");

    @media (min-resolution: @retina-dpi), (min-resolution: @retina-dppx) {
      background-image: url("../img/bg-map-desktop@2x.jpg");
    }
  }
}
